.userList {
    margin: 0 auto;
    width: 60vw;
}

.userList table {
    width: 100%;
    border-collapse: collapse;
    border-spacing: 0;
    height: auto;
}
.userList td,
.userList th {
    padding: 5px;
    border: 2px solid green;
}

.listAction {
    display: flex;
    justify-content: space-around;
}
